.coach {
  display: block;
  margin-left: 3px;
}

.coach img {
  width: 1.75em;
  height: auto;
  animation: scale 1s linear infinite;
}

@keyframes scale {
  0% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(0.97);
  }
  100% {
    transform: scaleY(1);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
