.mainContainer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  flex-direction: column;
  margin: auto;
  font-family: var(--font);
  font-size: medium;
}

.mainContainer a {
  font-size: 10px;
}

.mainWrapper {
  width: 100%;
}

.welcomeImage img {
  width: 250px;
  margin: 25px;
}

.section {
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
